import React, { Component } from 'react';

class DivSize extends Component {
  constructor(props){
    super(props);
    this.state = {
      height: 0
    };
  }
  onHeightChange(newHeight) {
    console.log("height change")
    this.setState({
      height: newHeight
    })
  }
  render() {
    return (
      <div
        className="test"
      >
        <div onHeightChange={this.onHeightChange.bind(this)}> Child one</div>
        <div> height {this.state.height}</div>
        {/* Size: <b>{this.state.height}px</b> but it should be 18px after the render {this.divElement.clientHeight} */}
      </div>
    )
  }
}

export default DivSize;


